@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Comfortaa', cursive;


}

.container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.box {
    width: 25%;
    height: 60vh;
    background-color: #a5bbdd;
    margin: auto;
    border-radius: 10px;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
    position: relative;
    overflow: hidden;
    transform: translate3d(0, 0, 0);
}

.inputData {
    padding: 20px;
}

.inputData input {
    padding: 10px;
    width: 100%;
    outline: none;
    border: none;
    font-size: 20px;
    border-radius: 10px;
}

i {
    font-size: 50px;
    margin-right: 10px;
    color: white;
}

.info {
    margin-top: 30%;
}

.info .temp {
    font-size: 30px;
    font-weight: 800;
    margin: 50px 0 20px 0;
    z-index: 12;
}

.tempmin_max {
    font-size: 14px;
    font-weight: 400;
    color: black;
}

.wave-one {
    opacity: 0.3;
    position: absolute;
    top: 145%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;
}

.wave-two {
    animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}

.wave-three {
    animation: drift 7000ms infinite linear;
    z-index: 3 !important;
    opacity: 0.1;
}

.box::after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0, 0, 0);
}

@keyframes drift {
    from {
        transform: rotate(0deg);
    }

    from {
        transform: rotate(360deg);
    }
}

@media screen and (max-width: 480px) {
    body {
        background-color: lightgreen;
    }

    .box {
        width: 75%;
        height: 60vh;
    }

    .info {
        margin-top: 40%;
    }

    .info .temp {
        margin: 70px 0 30px 0;
    }

    .wave-one {
        top: 125%;

    }
}